.menu_down {
  position: absolute;
  left: 0%;
  right: 0%;
  bottom: 0px;
  height: 50px;
  bottom: 0%;
  display: flex;
  justify-content: space-around;
  align-items: space-around;

  button, a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    border: none;
    outline: none;
    cursor: pointer;
    color: #FFFFFF;
    background: #374C5F;
  
    img {
      width: 20px;
    }
  }

  .refresh {
    position: relative;
    img {
      width: 32px;
    }
  }

  .overlay {
    position: absolute;
    width: 100%;
    height: 60px;
    top: -10px;
    left: 0;
    display: flex;
    justify-content: center;
    background: #5A9759;

  }
}

