
main.Home {
  .title {
    width: 100%;
    height: 60px;
    text-align: center;
  }

  .content {
    width: 100%;
    z-index: 1;
    text-align: center;
  }

  .trouble {
    display: flex;
    justify-content: space-between;
    padding: 16px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    img {
      width: 50px;
    }

    .info {
      width: 100%;
      max-width: calc( 100% - 70px);
      font-size: 12px;
      line-height: 20px;
    }
  }

  .alarm {
    img {
      filter: invert(54%) sepia(28%) saturate(4189%) hue-rotate(326deg) brightness(121%) contrast(104%);
    }
    .info {
      color: #FF6766;
    };
  }
  
  .back_lane_1, .back_lane_2 {
    background: #6e2b2b;
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    left: 50px;
    transform: rotate(315deg);
    // opacity: 0.12;
    width:200%;
  }

  .back_lane_1{
  
  }
  
  .back_lane_2{
    left: 250px;
    background-color: #212530;

  }
}
